import { LoadingintercepterService } from './../loadingintercepter.service';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router, RouterEvent } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {
  cakeid:any;
  checkoutDetails:any = {};
  checkoutCart:any = [];
  totalPrice:any;
  public showOverlay = true;
  // ingrediants:any = [];
  constructor(private route:ActivatedRoute, private http: HttpClient, private router: Router, private intercept:LoadingintercepterService) { 
    this.cartItems();
    // this.cakeid = this.route.snapshot.params['cakeid'];
    // console.log(this.cakeid);
  }

  ngOnInit(): void {
    // this.intercept.navigationInterceptor();
  }

  orderNow() {

    console.log(this.checkoutDetails);
  }

  cartItems() {
    let apiUrl = `https://apifromashu.herokuapp.com/api/cakecart`;
    this.http.post(apiUrl, {}).subscribe(
      (response: any) => {
        this.checkoutCart = response.data;
        // console.log("rr",response.data);
        this.checkoutDetails['data'] = response.data
        // this.checkoutDetails['ingradiants'] = response.data
        this.checkoutDetails.totalPrice = response.data.reduce((acc:any, value:any) => acc + value.price,0);
        this.showOverlay = false;


      },
      (error:any) => {
        console.log('this is response' + error);
      }
    );
  }

}
